<template>
	<div v-if="this.buyItems" class="team mt-10">
		<h1 class="subheading grey--text text-center px-2">Корзина</h1>

		<v-container class="my-5" fluid>

			<v-row>
				<v-col>
					<v-flex v-for="item in this.buyItems" :key="item.id">
						<v-card hover flat 
						class="d-inline-flex align-center text-xs-center ma-3 text-center">
							<v-responsive class="py-4 px-4">
								<v-avatar tile size="200" class="grey lighten-2">
									<img :src="item.img">
								</v-avatar>
							</v-responsive>
								<v-card-text class="d-inline-flex align-center justify-space-around" style="width:900px">
									<div class="subheading text-left font-weight-bold">{{ item.name }}</div>
									<v-text-field
									:rules="inputRules"
									type="number"
									style="max-width:15%;margin-top:35px;"
									label="К-ть"
									v-bind:placeholder="item.amount.toString()"
									@keypress.prevent.enter="checkAmount(item.amount, item.price, item.id)"
									outlined
									></v-text-field>
									<div>
										<h2 class="black--text text-left">{{ item.price.length > 3 ? item.price[0]+','+item.price.substr(1) : item.price }} грн</h2>
									</div>
										<v-icon @click.prevent="removeItem(item.amount, item.price, item.id)" medium right>close</v-icon>
								</v-card-text>
						</v-card>
					</v-flex>
				</v-col>
			</v-row>
				<v-container class="my-5">
					<v-row dense>
						<v-col>
							<router-link class="d-inline-flex black--text" style="text-decoration:none;" to="/">
								<v-icon medium left style="color:black">first_page</v-icon>
								<h4>Повернутися до покупок</h4>
							</router-link>
						</v-col>
						<v-col class="text-right">
								<h3 v-if="this.sum.reduce((cur,next)=>cur+=next,0)>1000" class="green--text">Доставка вашого замовлення безкоштовна</h3>
								<h4 v-if="this.sum.reduce((cur,next)=>cur+=next,0)>4000">Ваша знижка: {{Math.floor(this.sum.reduce((cur,next)=>cur+=next,0)/100*10)}} грн.</h4>
						</v-col>
					</v-row>
					<v-row dense>
						<v-col class="submit d-grid text-right">
							<h2 class="py-5">Всього: {{this.sum.length !==0 || this.sum.reduce((cur,next)=>cur+=next,0)>4000?this.sum.reduce((cur,next)=>cur+=next,0)-Math.floor(this.sum.reduce((cur,next)=>cur+=next,0)/100*10):''}} грн</h2>
							<button 
							depressed
							rounded
							type="submit" 
							class="btn white--text" 
							style="width:25%;height:2.5rem;"
							@click = "buyAllItems"
							>
							Оформити
							</button>
						</v-col>
					</v-row>
				</v-container>
		</v-container>
	
	</div>
	<div v-else class="d-inline-flex justify-center align-center" style="width:100%; height:100%">
		<h1 class="font-weight-bold" style="font-size:5rem;font-family:'Pacifico', cursive;">Корзина Пуста</h1>	
	</div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'

export default {
	name: 'ShoppingCart',
	data() {
		return {
			buyItems: [],
			amount: null,
			sum: [],
			newAmount: null,
			inputRules: [
				v => !!v || 'Введіть к-ть',
        v => v > 0 || 'Недостатньо товару'
			],
		}
	},
	computed: {
		...mapGetters(['allItems','shopping_cart_state']),
		},
	created() {
		if (this.buyItems.length === 0) {
          return this.buyItems = JSON.parse(localStorage.getItem('shopping_cart'))
        } else {
          return [];
				}
	},
	methods: {
		...mapActions(['editData','removeItemData']),
		buyAllItems() {
			localStorage.setItem('shopping_cart',JSON.stringify([]))
			this.$router.push('/')
			this.$store.commit('removeItemData')
		},
		removeItem(amount,price,id) {
			const index = this.buyItems.findIndex(item => item.id === id)
			this.buyItems.splice(index,1)
			localStorage.setItem('shopping_cart',JSON.stringify(this.buyItems))
			this.removeItemData(id)
		},
		checkAmount(amount,price,id) {
			let value = parseFloat(event.target.value)
			price = parseFloat(price.replace(' ',''))
			if(value > 0){
				this.newAmount = amount - value
				let evenlySum = price * value
				this.sum.push(evenlySum)
			}
			let res = []
			if(this.buyItems.length === 1){
				this.buyItems
				.filter(item => item.id === id)
				.map(item => {
					var obj = {
						id: item.id,
						name: item.name, 
						code_item: item.code_item, 
						img: item.img, 
						price: item.price, 
						rate: item.rate, 
						comments: item.comments, 
						amount: this.newAmount
					}
					res.push(obj)
					})
					localStorage.setItem(`${id}`,JSON.stringify(res))
					localStorage.setItem('shopping_cart',JSON.stringify(res))
					this.editData(res)					
			} else {
				this.buyItems
				.filter(item => item.id === id)
				.map(item => {
					var obj = {
						id: item.id,
						name: item.name, 
						code_item: item.code_item, 
						img: item.img, 
						price: item.price, 
						rate: item.rate, 
						comments: item.comments, 
						amount: this.newAmount
					}
					res = obj
				})
				let less = this.buyItems
				.filter(item => item.id !== id).map(item => localStorage.getItem(`${item.id}`))
				less = JSON.parse(less)
				localStorage.setItem(`${id}`,JSON.stringify(res))
				const items = []
				items.push(less,res) 
				localStorage.setItem('shopping_cart',JSON.stringify(items)) 
				this.editData(items)
			}
		}
	},
}
</script>

