const state = {
	items: [
		{ id: 1, name: 'Автомагнітола Shuttle SDUD-6970', code_item: '5243', img: '/item-1.png', price: '2 420', rate: 4, comments: 1, amount: 7, action: false },
		{ id: 2, name: 'Відеореєстратор Gazer H521', code_item: '1249', img: '/item-2.png', price: '2 999', rate: 2, comments: 4, amount: 0, action: true },
		{ id: 3, name: 'Моторне масло Total Quartz 5000 15W-40 (4 л)', code_item: '7948', img: '/item-3.png', price: '649', rate: 1, comments: 7, amount: 12, action: false },
		{ id: 4, name: 'Набір чохлів KEGEL-BLAZUSIAK', code_item: '0135', img: '/item-4.png', price: '7 515', rate: 5, comments: 0, amount: 15, action: false },
		{ id: 5, name: 'Сигналізація King Giordon GD908S', code_item: '3428', img: '/item-5.png', price: '1 699', rate: 3, comments: 12, amount: 0, action: true },
		{ id: 6, name: 'Шини автомобільні Hankook Winter I`Pike RS2 W429 195-65 R15', code_item: '4013', img: '/item-6.png', price: '1 329', rate: 3, comments: 0, amount: 11, action: false }
	],
	shopping_cart: []
}

const getters = {
	allItems: (state) => state.items,
	shopping_cart_state: (state) => state.shopping_cart
}

const actions = {
	addItem({commit}, item) {
		commit('addItem', item)
	},
	editData({commit}, item) {
		commit('editData', item)
	},
	removeItemData({commit}, id){
		commit('removeItemData', id)
	}
}

const mutations = {
	addItem: (state, item) => {
		if (!state.shopping_cart.includes(item) && item !== undefined) {
			state.shopping_cart.push(item)
		}
	},
	editData: (state, payload) => {
		if(state.shopping_cart[1]!==undefined){
			for(let newItem of payload) {
				const index = state.items.findIndex(item => item.id === newItem.id)
				if(index !== -1) {
					state.items.splice(index, 1, newItem)
				}
				const indexShoppingCart = state.shopping_cart.findIndex(item => item.id === newItem.id)
				if(indexShoppingCart !== -1) {
					state.shopping_cart.splice(indexShoppingCart, 1, newItem)
				}
			}
		}
	},
	removeItemData: (state, id) => {
		const index = state.shopping_cart.findIndex(item => item.id === id)
		if (index !== -1) {
			state.shopping_cart.splice(index, 1)
		}
		state.shopping_cart.filter(item=> item.length>0 || item === [])
	},
}

export default {
	state,
	getters,
	actions,
	mutations
}