const plugin = store => {

	store.subscribe((mutation, state) => {
		// Save to local storage all items in the cart state
		if ('addItem' === mutation.type) {
			let record = []
			for (let item of state.items.shopping_cart) {
				if(item !== undefined) record.push(item)
			}
			try {
				localStorage.setItem('my_site_cart', JSON.stringify(record));
			// eslint-disable-next-line no-empty
			} catch (e) {
				console.log(e)
			}
		}

		// When cart is edit, get the state from local storage if exists
		if ('editData' === mutation.type) {
			let storage = false;
			let localstorage = false;
			try {
				storage = JSON.parse(localStorage.getItem('my_site_cart')) || false;
				localstorage = JSON.parse(localStorage.getItem('shopping_cart')) || false;
				if (storage.length !== localstorage.length) {
					localstorage = JSON.parse(localstorage);
					store.commit('addItem', localstorage)
				} else {
					storage = JSON.parse(storage)
				}
			// eslint-disable-next-line no-empty
			} catch (e) {
				console.log(e)
			}
		}

		if ('removeItemData' === mutation.type) {
			try {
				let localstorage = localStorage.getItem('shopping_cart') || false;
				localStorage.setItem('my_site_cart', JSON.stringify(localstorage))
				// eslint-disable-next-line no-empty
			} catch (e) {
				console.log(e)
			}
		}
	});
}

export default plugin;