<template>
	<div class="team">
		<h1 class="subheading grey--text text-center">Каталог</h1>

		<v-container v-if="allItems" class="my-5">
			<div 
			class="d-inline-flex justify-end py-5" style="width:100%">
				<router-link style="text-decoration: none" v-bind:to="{name: 'shoppingcart'}">
						<v-chip v-if="amount_items > 0" style="background-color:green;cursor:pointer;">
							<v-icon center>shopping_cart</v-icon>
							<p>{{ amount_items }}</p>
						</v-chip>
						<v-chip v-else style="cursor:pointer;">
							<v-icon center>shopping_cart</v-icon>
						</v-chip>
				</router-link>
			</div>

			<v-layout row wrap>
				<v-flex xs12 sm6 md4 lg3 v-for="item in allItems" :key="item.id">
						<v-card hover flat class="text-xs-center ma-3 text-center">
							<v-responsive class="pt-4">
								<v-avatar tile size="200" class="grey lighten-2">
									<img :src="item.img">
								</v-avatar>
							</v-responsive>
								<v-card-text justify-center>
									<div class="subheading text-left">{{ item.name }}</div>
									<div v-if="item.amount > 0" style="color : #5bbb4b" class="subheading text-left">
										<h3><v-icon style="color : #5bbb4b">check</v-icon> В наявності</h3>
									</div>
									<div v-else style="color : #ff0030" class="subheading text-left">
										<h3>Немає в наявності</h3>
									</div>
									<div class="code--text text-left d-flex flex-row">
										<h3>Код товару : {{ item.code_item }}</h3>
									</div>
									<div class="d-flex justify-space-around">
										<div v-for="item in item.rate" :key="item.rate" class="d-inline">
											<v-icon large color="yellow accent-4">star</v-icon>
										</div>
											<p v-if = "item.comments === 0 || item.comments > 1 " class="my-auto ml-auto" style="text-decoration: underline; color: #131d5f">{{ item.comments }} відгуків</p>
											<p v-else class="my-auto ml-auto" style="text-decoration: underline; color: #131d5f">{{ item.comments }} відгук</p>
									</div>
									<div class="d-inline-flex" v-if="item.action === true">
										<h2 class="code--text mx-3" style="text-decoration-line:line-through;">{{ item.price.length > 3 ? item.price[0]+','+item.price.substr(1) : item.price }} грн</h2>
										<h2 class="black--text mx-3">{{ item.price.length > 3 ? item.price[0]+','+item.price.substr(1) : item.price }} грн</h2>
									</div>
									<div v-else>
										<h2 class="black--text text-left">{{ item.price.length > 3 ? item.price[0]+','+item.price.substr(1) : item.price }} грн</h2>
									</div>
								</v-card-text>
							<v-card-actions @click="onAddItem(item, item.id)" v-if="item.amount > 0" class="justify-center">
								<v-btn color="btn white--text" style="width:100%">
									<v-icon small left>shopping_cart</v-icon>
									<span class="text-center">Купити</span>
								</v-btn>	
							</v-card-actions>
							<v-card-actions v-else class="justify-center">
									<v-btn disabled style="width:100%">
										<v-icon small left>shopping_cart</v-icon>
										<span class="text-center">Купити</span>
									</v-btn>	
							</v-card-actions>
						</v-card>
					</v-flex>
				</v-layout>

		</v-container>

		<div v-else>
			<h1>Грузимось....</h1>
		</div>
	</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
	name: 'Dashboard',
	data(){
		return {
			item_id:'',
			shopping_cart: [],
			amount_items: null,
			items: []
		}
	},
	methods: {
		...mapActions(['addItem']),
		onAddItem(item,id) {
			this.item_id = id
			this.addItem(item)
			if(!this.shopping_cart.includes(item)) {
				this.shopping_cart.push(item)
				this.amount_items = this.amount_items + 1
			}
			localStorage.setItem(`${item.id}`,JSON.stringify(item))
			localStorage.setItem('shopping_cart',JSON.stringify(this.shopping_cart))
		}
	},
	computed: {...mapGetters(['allItems'])},
	mounted() {
		localStorage.items = JSON.stringify(this.allItems);
	}
	}
</script>
